.widget {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 10px;
  justify-content: space-around;
  height: 100px;
  margin-right: 40px;
  background-color: rgb(156, 228, 204);



  .left,
  .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
   
    

    .title {
      font-weight: bold;
      font-size: 14px;
      color: rgb(160, 160, 160);
      color:black;
      padding:5px;
      font-size: 20px;

    }

    .counter {
      font-size: 28px;
      font-weight: 300;
    }

    .link {
      width: max-content;
      font-size: 12px;
      border-bottom: 1px solid gray;
    }

    .percentage {
      display: flex;
      align-items: center;
      font-size: 14px;

      &.positive {
        color: green;
      }
      &.negative {
        color: red;
      }
    }

    .icon {
      font-size: 18px;
      padding: 5px;
      border-radius: 5px;
      align-self: flex-end;
    }
  }
}
